import React, { Component } from 'react';
import ScrollBar from 'react-perfect-scrollbar';
import 'react-perfect-scrollbar/dist/css/styles.css';

import events from './events.svg';
import mlive from './mlive.svg';
import cse from './cse.svg';
import a2hosting from './a2hosting.png';
import './Navbar.css';

var mlive_link = 'http://www.mlive.com/news/ann-arbor/index.ssf/2015/12/ann_arbor-area_high_school_stu.html';
var cse_link = 'https://www.eecs.umich.edu/eecs/about/articles/2016/ArborHacks.html';
var a2hosting_link = 'https://a2hosting.com';

class NavBar extends Component {
  render = () => {
    return (
      <div className='scrollarea'>
        <ScrollBar>
          <div className='events'>
            <a target='_blank' alt='events'>
              <img className='events' src={events} alt='events' />
            </a>
          </div>
          <div className='mlive'>
            <a target='_blank' href={mlive_link} alt='mlive'>
              <img className='mlive' src={mlive} alt='mlive' />
            </a>
          </div>
          <div className='cse'>
            <a target='_blank' href={cse_link} alt='cse'>
              <img className='cse' src={cse} alt='cse' />
            </a>
          </div>
          <div className='a2hosting'>
            <a target='_blank' href={a2hosting_link} alt='a2hosting'>
              <img className='a2hosting' src={a2hosting} alt='a2hosting' />
            </a>
          </div>
          <div className='contact'>
            <a target='_blank' href={'/contact'} alt='contact'>
              <img className='contact' src={events} alt='contact' />
            </a>
          </div>
          <div className='algorhythm'>
            <a target='_blank' href={'/algorhythm'} alt='algorhythm'>
              <img className='algorhythm' src={mlive} alt='algorhythm' />
            </a>
          </div>
        </ScrollBar>
      </div>
    );
  }
}

export default NavBar;
