class Form extends Component {
  constructor(props) {
    super(props);
    this.el = null;
  }

  componentDidMount = () => {
    if (this.el) {
      typeformEmbed.makeWidget(this.el, 'https://arborhacks.typeform.com/to/DAMj1h', {
        hideFooter: true,
        hideHeaders: true,
        opacity: 75
      });
    }
  }

  render = () => {
    return (
      <div ref={(el) => this.el = el} className='typeform'>
      </div>
    );
  }
}

class Flyer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      scrollTop: 0
    };
  }

  updateScroll = () => {
    let supportPageOffset = window.pageYOffset !== undefined;
    let isCSS1Compat = ((document.compatMode || '') === 'CSS1Compat');
    let scroll = supportPageOffset ?
        window.pageYOffset : isCSS1Compat ?
        document.documentElement.scrollTop : document.body.scrollTop;
    this.setState({ scrollTop: scroll });
  }

  componentDidMount = () => {
    window.addEventListener('scroll', this.updateScroll);
  }

  componentWillUnmount = () => {
    window.removeEventListener('scroll', this.updateScroll);
  }

  render = () => {
    let opacity = {
      opacity: Math.max(0, Math.min(0.85, (window.innerHeight -
          this.state.scrollTop) / window.innerHeight))
    };
    return (
      <a href='/algorhythm'>
        <img src={algoRhythm} className='flyer' style={opacity} onClick={this.props.handler} alt='flyer' />
      </a>
    );
  }
}

