import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown/with-html';
import hljs from 'highlight.js/lib/highlight';
import python from 'highlight.js/lib/languages/python';

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


import 'highlight.js/styles/atom-one-dark.css';
import './Algorhythm.css';

hljs.registerLanguage('python', python);

class Algorhythm extends Component {
  constructor(props){
    super(props)

    this.state = { temp1: null, temp2: null, temp3: null }
  }

  componentWillMount = () => {
    const temp1Path = require('./temp1.md');
    const temp2Path = require('./temp2.md');
    const temp3Path = require('./temp3.md');

    fetch(temp1Path) .then(response => { return response.text() }) .then(text => { this.setState({ temp1: text }) })
    fetch(temp2Path) .then(response => { return response.text() }) .then(text => { this.setState({ temp2: text }) })
    fetch(temp3Path) .then(response => { return response.text() }) .then(text => { this.setState({ temp3: text }) })

  }

  componentDidMount() {
    hljs.initHighlightingOnLoad();
    document.querySelectorAll('pre code').forEach((block) => {
      hljs.highlightBlock(block);
    });
  }

  componentDidUpdate() {
    document.querySelectorAll('pre code').forEach((block) => {
      hljs.highlightBlock(block);
    });
  }

  openNav = () => {
    if (
      document.getElementsByClassName("navigationBar") &&
      document.getElementsByClassName("mainText")
    ) {
      var elems = document.getElementsByClassName("navigationBar");
      for(var i = 0; i < elems.length; ++i){
        elems[i].style.width = "20%";
      }
      var elems = document.getElementsByClassName("mainText");
      for(var i = 0; i < elems.length; ++i){
        elems[i].style.width = "80%";
        elems[i].style.left = "20%";
      }
    }
  };

  closeNav = () => {
    if (
      document.getElementsByClassName("navigationBar") &&
      document.getElementsByClassName("mainText")
    ) {
      var elems = document.getElementsByClassName("navigationBar");
      for(var i = 0; i < elems.length; ++i){
        elems[i].style.width = "0px";
      }
      var elems = document.getElementsByClassName("mainText");
      for(var i = 0; i < elems.length; ++i){
        elems[i].style.width = "100%";
        elems[i].style.left = "0px";
      }
    }
  };

  render = () => {
    return (
      <div>
        <div className='navigationBar'>
          <ul className='navigationBar' >
            <li><a href='javascript:void(0)' className='closetbn' onClick={this.closeNav}>&times;</a></li>
            <li><a className='navHeader' href="/algorhythm">Algorhythm Home</a></li>
            <li><a className='navHeader' href="/algorhythm/intro-to-python">Python: Overview</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/hello-world">Python: Introduction</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/variables">Python: Variables</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/arrays">Python: Arrays</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/if-else">Python: If-Else Statements</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/loops">Python: Loops</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/functions">Python: Functions</a></li>
            <li><a className='navHeader' href="/algorhythm/intro-to-pygame">Intro to Pygame</a></li>
              <li><a className='navLink' href="/algorhythm/intro-to-python/general-introduction">Pygame: Introduction</a></li>
            <li><a className='navHeader' href="/algorhythm/piano-key-project">Piano Key Project</a></li>
          </ul>
        </div>
        <div className='mainText'>
          <div>
            <a href='javascript:void(0)' className='opentbn'>
              <span style={{ fontSize: "30", cursor: "pointer" }} onClick={this.openNav}>&#9776; open</span>
            </a>
          </div>
          <div style={{ marginLeft: '5px' }}>
            <Switch>
              <Route path='/algorhythm/intro-to-python/hello-world'>
                <p style={{ textAlign: 'left' }}>
                  <ReactMarkdown escapeHtml={false} source={this.state.temp3} />
                </p>
              </Route>
              <Route path='/algorhythm/intro-to-python'>
                <p style={{ textAlign: 'left' }}>
                  <ReactMarkdown escapeHtml={false} source={this.state.temp2} />
                </p>
              </Route>
              <Route path='/algorhythm'>
                <p style={{ textAlign: 'left' }}>
                  <ReactMarkdown escapeHtml={false} source={this.state.temp1} />
                </p>
              </Route>
            </Switch>
          </div>
        </div>
      </div>
    );
  }
}

export default Algorhythm;
